.contaiLaurels{
    width: 100vw;
    height: auto;
    background-image: url("../../public/bg.png");
}
.containerLaurels{
    margin-top: 10vh;
    padding-top: 20vh;
    width: 90vw;
    margin: auto;
    height: auto;
    min-height: 100vh;
    display: grid;
    grid-template-columns: auto  50% ;
    justify-content: space-between;
}
.boxTextLaurels{
    width: 35vw;
}
.boxTextLaurels h4{
    color: rgb(89, 86, 86);
    font-size: 1.2em;
}
.boxTextLaurels h1{
    font-size: 4em;
    margin-top: 1vh;
    margin-bottom: 8vh;
    font-family: 'Cinzel', serif;
    color: rgb(196, 172, 131);
}
.itens{
    display: flex;
    justify-content: start;
}
.itens img{
    width: 60px;
    margin-right: 2vw;
}
.item h3{
    font-size: 1em;
}
.boxImgLaurels img{
    width: 600px;
}
@media (max-width: 1280px) {
    
    .boxImgLaurels img {
        width: 500px;
    }
    
    
}

@media (max-width: 1072px) {
    
    .boxImgLaurels img {
        width: 450px;
    }
    
    .containerLaurels {
        margin-top: 10vh;
        padding-top: 20vh;
        width: 90vw;
        margin: auto;
        height: auto;
        min-height: 100vh;
        display: grid;
        grid-template-columns: auto 50%;
        justify-content: space-between;
    }
}
@media (max-width: 953px) {
    
    .boxImgLaurels img {
        width: 450px;
    }
    
    .containerLaurels {
        grid-template-columns: auto 40%;
        justify-content: space-around;
        min-height: 90vh;
    }
    .boxTextLaurels h1 {
        font-size: 3.5em;
        margin-bottom: 6vh;
    }
}
@media (max-width: 800px) {
    
    .boxTextLaurels {
        width: 80vw;
        margin-bottom: 4vh;
    }
    .containerLaurels {
        grid-template-columns: auto ;
        justify-content: space-around;
        min-height: 90vh;
        padding-bottom: 4vh;
    }
    .boxTextLaurels h1 {
        font-size: 3.5em;
        margin-bottom: 6vh;
    }
}
@media (max-width: 530px) {
    
    .boxImgLaurels img {
        width: 350px;
    }
    
}
@media (max-width: 390px) {
    
    .boxImgLaurels img {
        width: 300px;
    }
    .boxTextLaurels {
        width: 90vw;
        margin-left: 4vw;
        margin-bottom: 4vh;
    }
    
}
@media (max-width: 378px) {
    
    .boxImgLaurels img {
        width: 300px;
    }
    .boxTextLaurels {
        width: 100vw;
        margin-left: 4vw;
        margin-bottom: 4vh;
    }
    .itensText{
        width: 60vw;
    }
    
}
@media (max-width: 325px) {
    
    .boxImgLaurels img {
        width: 250px;
    }
    
    .itensText{
        width: 60vw;
    }
    
}